@import url(https://stackpath.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
/* @import "https://use.fontawesome.com/releases/v5.8.1/css/all.css"; */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
html,body{margin:0; padding:0; position: relative; overflow-x:hidden}
*{box-sizing: border-box; }
img{width:100%; height: auto; display: block;}
a,a:hover,a:focus{text-decoration:none}
.cursor{cursor: pointer;}

.title{color:#fff}

header{padding:15px; display: flex; justify-content: space-between; align-items: center; position:absolute; top:0; width:100%;z-index: 1;}

header nav a{color:#fff; margin: 0 15px; position:relative;padding:5px 0; font-family:'Aharoni';}
header nav a:hover{color:#fff;}
header nav a:before{content: ''; position: absolute; width:0; height: 2px; left: 0; background: #fff; bottom: 0; -webkit-transition: all 1s ease; transition: all 1s ease;}
header nav a.active:before, header nav a:hover:before{width:100%;}

.homepage{display: flex; justify-content: center; align-items: center; }
.titleImg{position:relative;}
.titleImg img {margin:0}
.titleImg h2 { position: absolute; -webkit-transform: rotate(90deg); transform: rotate(90deg); left: -201px; bottom: 0; color: #fff; font-size: 4rem; text-align: center; width: 100%; }
.logo{max-width:120px;}
.logo:hover{-webkit-animation: shake 0.5s;animation: shake 0.5s; -webkit-animation-iteration-count: infinite; animation-iteration-count: infinite; }
footer{position: absolute; bottom: 0; width: 100%; display: block;}

.main{/*padding:56px 0 0;*/ background: #b41bb9; min-height: 100vh; display: flex; align-items: center; justify-content: space-around; position: relative;}
.main > div{width:100%}
.imgbox:after {
content: '';
position: absolute;
top: 0;
left: 0;
height: 100%;
width: 100%;
background: rgba(0,0,0,0);
z-index: 1;
}
.imgbox{width: 25vw; min-width:308px; -webkit-transform: translateX(50%); transform: translateX(50%); position: relative; overflow: hidden; z-index: 1;}

.imgbox h2{color: #fff; -webkit-writing-mode: vertical-rl; -ms-writing-mode: tb-rl; writing-mode: vertical-rl; -webkit-text-orientation: mixed; text-orientation: mixed; position: absolute; z-index: 1; display: block; left: 0; margin: 0; height: 100%; text-align: center; margin-left: -28px; font-size: 13vh;}
.imgSection {display: flex; align-items: flex-end; justify-content: flex-end; background: #2f1433; height: 100vh; /*calc(100vh - 56px);*/ max-width: 30%;}
.textSection { color: #fff; padding-left: 20%; height: 100vh; align-items: baseline; flex-direction: column; display: flex; justify-content: center; background-image: url(/static/media/ajitsingh.b9f7ad06.jpg); background-size: cover; background-position: right; background-repeat: no-repeat; position: relative; z-index: 0; font-family:'Aharoni';}
.textSection h1{font-size:4.1rem; margin: 0}
.textSection h2{font-size:3.1rem; margin: 0}
.textSection h3{font-size:2.1rem; margin: 0}
.textSection:before {
content: '';
position: absolute;
background: rgba(203, 72, 221,0.8);
height: 100%;
width: 100%;
left: 0; z-index: -1;
}

.socialicon li{ display: inline-block;}
.socialicon { list-style: none; padding:0 15px; margin: 0; max-width:2000px;}
.socialicon a { display: inline-block; color: #fff; font-size: 30px; margin: 0 5px; text-align: center; border-radius: 5px; }
/*.socialicon a:hover{animation: shake 0.5s; animation-iteration-count: infinite; }*/
.bt{padding: 8px 15px; font-size:1.3rem; position:relative;}
.bt-outline{border:0 solid #fff; background: transparent; color: #fff; -webkit-transition: all 0.5s ease; transition: all 0.5s ease;}
.bt-outline:hover{border-radius: 15px;}
/* .bt-outline:before { border-color:transparent #fff #fff transparent; border-style: solid; position: absolute; content: ''; height: 100%; width: 100%; left: 2px; top: 2px; border-width:3px; transition: all 0.5s ease;} */
.bt-outline:after { border-color:#fff transparent transparent #fff ; border-style: solid; position: absolute; content: ''; height: 100%; width: 100%; left: -2px; top: -2px; border-width:3px; -webkit-transition: all 0.5s ease; transition: all 0.5s ease;}

/* .bt-outline:hover:before { border-color:#fff #fff transparent transparent  ; left: -2px; top: -2px;}
.bt-outline:hover:after { border-color:transparent transparent #fff #fff ; left: 2px; top: 2px; } */
.bt-outline:hover:before { border-color:#fff #fff transparent transparent  ; left: 2px; top: -2px;}
.bt-outline:hover:after { border-color:transparent transparent #fff #fff ; left: -2px; top: 2px; }

@media (max-width:1000px){
.socialicon{width:45px}
}
@media (max-width:760px){
.imgSection{display: none;}
.socialicon{width:100%}
.textSection{ text-align: center; padding: 15px; align-items: center;}
}

@-webkit-keyframes shake {
0% { -webkit-transform: translate(1px, 1px) rotate(0deg); transform: translate(1px, 1px) rotate(0deg); }
/*10% { transform: translate(-1px, -2px) rotate(-1deg); }*/
20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); transform: translate(-3px, 0px) rotate(1deg); }
/*30% { transform: translate(3px, 2px) rotate(0deg); }*/
40% { -webkit-transform: translate(1px, -1px) rotate(1deg); transform: translate(1px, -1px) rotate(1deg); }
/*50% { transform: translate(-1px, 2px) rotate(-1deg); }*/
60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); transform: translate(-3px, 1px) rotate(0deg); }
/*70% { transform: translate(3px, 1px) rotate(-1deg); }*/
80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); transform: translate(-1px, -1px) rotate(1deg); }
/*90% { transform: translate(1px, 2px) rotate(0deg); }*/
100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); transform: translate(1px, -2px) rotate(-1deg); }
}

@keyframes shake {
0% { -webkit-transform: translate(1px, 1px) rotate(0deg); transform: translate(1px, 1px) rotate(0deg); }
/*10% { transform: translate(-1px, -2px) rotate(-1deg); }*/
20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); transform: translate(-3px, 0px) rotate(1deg); }
/*30% { transform: translate(3px, 2px) rotate(0deg); }*/
40% { -webkit-transform: translate(1px, -1px) rotate(1deg); transform: translate(1px, -1px) rotate(1deg); }
/*50% { transform: translate(-1px, 2px) rotate(-1deg); }*/
60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); transform: translate(-3px, 1px) rotate(0deg); }
/*70% { transform: translate(3px, 1px) rotate(-1deg); }*/
80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); transform: translate(-1px, -1px) rotate(1deg); }
/*90% { transform: translate(1px, 2px) rotate(0deg); }*/
100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); transform: translate(1px, -2px) rotate(-1deg); }
}


.textSection span {
display: inline-block;
text-shadow: 0 0 0 whitesmoke;
-webkit-animation: smoky 3s 1s both;
        animation: smoky 3s 1s both;
}

.textSection span:nth-child(even){
-webkit-animation-name: smoky-mirror;
        animation-name: smoky-mirror;
}

@-webkit-keyframes smoky {
0% {
-webkit-transform:
	translate3d(2rem,-1rem,0)
	rotate(-40deg)
	skewX(70deg)
	scale(1.5);
        transform:
	translate3d(2rem,-1rem,0)
	rotate(-40deg)
	skewX(70deg)
	scale(1.5);
-webkit-filter: blur(23px);
        filter: blur(23px);
text-shadow: 0 0 20px whitesmoke;
opacity: 0;
}
to {
text-shadow: 0 0 0 whitesmoke;    
}
}

@keyframes smoky {
0% {
-webkit-transform:
	translate3d(2rem,-1rem,0)
	rotate(-40deg)
	skewX(70deg)
	scale(1.5);
        transform:
	translate3d(2rem,-1rem,0)
	rotate(-40deg)
	skewX(70deg)
	scale(1.5);
-webkit-filter: blur(23px);
        filter: blur(23px);
text-shadow: 0 0 20px whitesmoke;
opacity: 0;
}
to {
text-shadow: 0 0 0 whitesmoke;    
}
}

@-webkit-keyframes smoky-mirror {
0% {
-webkit-transform:
	translate3d(2rem,-1rem,0)
	rotate(-40deg) 
	skewX(-70deg)
	scale(2);
        transform:
	translate3d(2rem,-1rem,0)
	rotate(-40deg) 
	skewX(-70deg)
	scale(2);
-webkit-filter: blur(23px);
        filter: blur(23px);
	text-shadow: 0 0 20px whitesmoke;
opacity: 0;}
to {
text-shadow: 0 0 0 whitesmoke; 
}
}

@keyframes smoky-mirror {
0% {
-webkit-transform:
	translate3d(2rem,-1rem,0)
	rotate(-40deg) 
	skewX(-70deg)
	scale(2);
        transform:
	translate3d(2rem,-1rem,0)
	rotate(-40deg) 
	skewX(-70deg)
	scale(2);
-webkit-filter: blur(23px);
        filter: blur(23px);
	text-shadow: 0 0 20px whitesmoke;
opacity: 0;}
to {
text-shadow: 0 0 0 whitesmoke; 
}
}

